import { observer } from 'mobx-react'

import {
  compose,
  branch,
  renderComponent,
} from 'recompose'

import Loader from 'features/components/common/loader/Loader'

type TIsLoading = (args: any) => boolean

/*
        
   :    isLoading
  . props => props.someModel.isFetching
    true,   .
   false,   ,    (  compose)
*/
export const withSpinnerWhileLoading = (isLoading: TIsLoading) =>
  compose(
    observer,
    branch(
      isLoading,
      renderComponent(Loader),
    ),
  )
